.search-page {
    margin: 0;
    padding: 0;
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
}

.search-page__content {
    margin: 0 auto;
    padding: 100px 0;
    width: 100%;
    max-width: var(--section_max_width);
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    gap: 100px;
}